<template>
  <el-form-item :prop="propskey" :label="inline ? '' : title" v-bind="formItemProps">
    <!-- -->
    <span v-if="type === 'text'" class="disabled-text">
      {{
        getDisabledValue({
          propskey,
          value: params[propskey],
          type,
          options
        })
      }}
    </span>
    <!--  -->
    <el-input
      v-else-if="type === 'input'"
      v-model="params[propskey]"
      @keyup.enter.native="$emit('enter')"
      v-bind="otherProps"
    />
    <!-- v-else-if="!type || type === 'input'" -->
    <!--  -->
    <el-input
      v-else-if="type === 'textarea'"
      type="textarea"
      v-model="params[propskey]"
      v-bind="otherProps"
    />
    <!--  -->
    <el-select v-else-if="type === 'select'" v-model="params[propskey]" v-bind="otherProps">
      <el-option
        v-for="option in options"
        :key="option.value"
        :label="option.label"
        :value="option.value"
        >{{ option.label }}</el-option
      >
    </el-select>
    <!--  -->
    <el-radio-group v-else-if="type === 'radio'" v-model="params[propskey]" v-bind="otherProps">
      <el-radio v-for="option in options" :key="option.value" :label="option.value">{{
        option.label
      }}</el-radio>
    </el-radio-group>
    <el-radio-group
      v-else-if="type === 'radioButton'"
      v-model="params[propskey]"
      v-bind="otherProps"
    >
      <el-radio-button v-for="option in options" :key="option.value" :label="option.value">{{
        option.label
      }}</el-radio-button>
    </el-radio-group>
    <!--  -->
    <el-checkbox-group
      v-else-if="type === 'checkbox'"
      v-model="params[propskey]"
      v-bind="otherProps"
    >
      <el-checkbox v-for="option in options" :label="option.value" :key="option.value">{{
        option.label
      }}</el-checkbox>
    </el-checkbox-group>
    <!-- ,css,iview,v-modelvalueonChangeonChangeelementelementvalue-format -->
    <el-date-picker
      v-else-if="type === 'date'"
      :type="otherProps.dateType"
      v-model="params[propskey]"
      v-bind="otherProps"
    ></el-date-picker>
    <el-switch
      v-else-if="type === 'switch'"
      v-model="params[propskey]"
      v-bind="otherProps"
    ></el-switch>
    <el-cascader
      v-else-if="type === 'cascader'"
      v-model="params[propskey]"
      v-bind="otherProps"
      :options="options"
    ></el-cascader>
    <el-input-number
      v-else-if="type === 'inputnumber'"
      v-model="params[propskey]"
      v-bind="otherProps"
    ></el-input-number>
    <!--  -->
    <template v-else slot-scope="{ value }">
      <slot :name="propskey" :value="params[propskey]"></slot>
    </template>
  </el-form-item>
</template>

<script>
export default {
  name: "XFormItem",
  props: {
    propskey: {
      type: String
    },
    title: {
      type: String
    },
    type: {
      type: String
    },
    options: {
      type: Array
    },
    formItemProps: {
      type: Object
    },
    otherProps: {
      type: Object
    },
    params: {
      type: Object
    },
    inline: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {};
  },
  created() {},
  mounted() {},
  methods: {
    // disabled
    getDisabledValue({ key, value, type, options }) {
      if (type === "select") {
        const option = options.find(item => item.key === value);
        return (option && option.label) || "";
      } else {
        return value;
      }
    }
  }
};
</script>
